import { signIn } from "@/lib/auth";

const GoogleSignin = () => {
  return (
    <form
      action={async () => {
        "use server";
        await signIn("google");
      }}
    >
      <button className="bg-slate-50 p-2 rounded flex gap-x-2 items-center" type="submit">
        <span>
          <img className="w-[20px]" src={"/google.png"} alt="" />
        </span>
        Signin with Google
      </button>
    </form>
  );
};

export default GoogleSignin;
